@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
*,*::after,*::before{box-sizing:border-box}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}:focus{outline:0}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration,input[type="search"]::-webkit-search-results-button,input[type="search"]::-webkit-search-results-decoration{-webkit-appearance:none;-moz-appearance:none}input[type="search"]{-webkit-appearance:none;-moz-appearance:none;box-sizing:content-box}textarea{overflow:auto;vertical-align:top;resize:vertical}audio,canvas,video{display:inline-block;*display:inline;*zoom:1;max-width:100%}audio:not([controls]){display:none;height:0}[hidden]{display:none}html{font-size:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%}a:focus{outline:thin dotted}a:active,a:hover{outline:0}img{border:0;-ms-interpolation-mode:bicubic}figure{margin:0}form{margin:0}fieldset{border:1px solid #c0c0c0;margin:0 2px;padding:0.35em 0.625em 0.75em}legend{border:0;padding:0;white-space:normal;*margin-left:-7px}button,input,select,textarea{font-size:100%;margin:0;vertical-align:baseline;*vertical-align:middle}button,input{line-height:normal}button,select{text-transform:none}button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:button;cursor:pointer;*overflow:visible}button[disabled],html input[disabled]{cursor:default}input[type="checkbox"],input[type="radio"]{box-sizing:border-box;padding:0;*height:13px;*width:13px}input[type="search"]{-webkit-appearance:textfield;box-sizing:content-box}input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none}button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0}textarea{overflow:auto;vertical-align:top}table{border-collapse:collapse;border-spacing:0}html,button,input,select,textarea{color:#222}::selection{background:#b3d4fc;text-shadow:none}img{vertical-align:middle}fieldset{border:0;margin:0;padding:0}textarea{resize:vertical}.chromeframe{margin:0.2em 0;background:#ccc;color:#000;padding:0.2em 0}html{margin-right:calc(-1 * (100vw - 100%))}body{overflow-x:hidden;min-width:320px;font-family:"Roboto",sans-serif;font-size:14px;line-height:20px;color:#000;background:repeat url(/registration-app/static/media/background.f32775a0.png)}

.App-ContentWrapper{display:flex;align-items:center;flex-direction:column;width:100%;max-width:1280px;padding:40px 32px;margin:0 auto}

.Header{width:100%;height:40px;background-color:#000}.Header-Wrapper{display:flex;align-items:center;height:100%;width:100%;padding:0 32px}

.Hamburger-Button{position:relative;display:flex;flex-direction:column;justify-content:space-around;width:2rem;height:2rem;background:transparent;border:none;cursor:pointer;padding:0;z-index:100}.Hamburger-Button span{width:2rem;height:0.25rem;background:#fff;border-radius:10px;transition:all 0.3s linear;position:relative;transform-origin:1px}.Hamburger-Button span:first-child{transform:rotate(0)}.Hamburger-Button span:nth-child(2){opacity:1;transform:translateX(0)}.Hamburger-Button span:nth-child(3){transform:rotate(0)}.Hamburger-Button_open span:first-child{transform:rotate(45deg)}.Hamburger-Button_open span:nth-child(2){opacity:0;transform:translateX(20px)}.Hamburger-Button_open span:nth-child(3){transform:rotate(-45deg)}

.Menu{position:absolute;top:0;left:0;z-index:99;min-height:100vh;width:100%;max-width:360px;padding-top:120px;background-color:#000}

.Navigation-Item:not(:last-child){border-bottom:1px solid #FFFFFF}.Navigation-Link{display:flex;align-items:center;min-height:60px;width:100%;padding:0 32px;font-weight:bold;font-size:18px;line-height:21px;letter-spacing:1px;color:#fff;text-decoration:none;transition:all 0.3s ease-out}.Navigation-Link:hover{color:#202020;background-color:#ebdabe}

.Blackout{position:absolute;top:0;right:0;bottom:0;left:0;z-index:10;background-color:#000;opacity:0.5}

.ClientList{display:flex;flex-direction:column;width:100%;max-width:960px;padding-top:32px;padding-bottom:10px;border-radius:10px;background-color:#fff}.ClientList-Heading{align-self:center;font-weight:500;font-size:24px;line-height:28px;text-transform:uppercase;margin-bottom:32px}.ClientList-TableWrapper{overflow:auto}.ClientList-Table{width:100%;min-width:500px}.ClientList-TableRow{border-bottom:1px solid #C9C9C9}.ClientList-TableRow:last-child{border-bottom:none}.ClientList-TableRow:hover:not(.ClientList-TableRow_HeaderRow){transition:background-color 0.1s linear;background-color:rgba(235,218,190,0.4)}.ClientList-TableHeader{padding:8px 32px 8px 0;text-align:left;font-weight:500;font-size:14px;line-height:16px;color:#9FA2B4;border-bottom:1px solid #9FA2B4}.ClientList-TableHeader:first-child{width:30%;padding-left:32px}.ClientList-TableHeader:nth-child(2){width:20%}.ClientList-TableHeader:nth-child(3){width:30%}.ClientList-TableHeader:nth-child(4){width:20%}.ClientList-TableData{padding:18px 32px 18px 0;font-size:14px;line-height:20px}.ClientList-TableData:first-child{padding-left:32px}.ClientList-CheckIcon.MuiSvgIcon-root{padding:2px;margin-bottom:-5px;border:1px solid #C9C9C9;border-radius:50%;line-height:26px;font-size:20px;color:#00ff00;align-self:flex-end;justify-self:flex-end}.ClientList-Empty{display:flex;flex-direction:column;align-items:center}.ClientList-EmptyImage{height:auto;width:100%;margin-bottom:24px;max-width:300px}.ClientList-EmptyMessage{margin-bottom:16px;font-weight:500;font-size:20px;line-height:23px;color:#2B2B2B}.ClientList-EmptyLink{margin-bottom:16px;font-size:14px;line-height:16px;color:#9374EB;text-decoration:underline}

.AboutMe{display:flex;flex-direction:column;width:100%;max-width:720px;padding:32px;border-radius:10px;background-color:#fff}.AboutMe-Name{align-self:center;margin-bottom:8px;font-weight:500;font-size:24px;line-height:28px}.AboutMe-Position{text-align:center;align-self:center;font-size:20px;line-height:24px;color:#404040}.AboutMe-Heading{margin:16px 0;font-size:20px;font-weight:bold;line-height:24px;color:#9374EB}.AboutMe-ContactList{margin-left:20px}.AboutMe-ContactItem{display:flex;justify-content:space-between;max-width:400px;margin-bottom:4px}.AboutMe-ContactItemValue{min-width:220px;text-decoration:none;color:inherit}@media screen and (max-width: 550px){.AboutMe-ContactItem{flex-direction:column;margin-bottom:8px;font-weight:500}.AboutMe-ContactItemValue{font-weight:normal}}.AboutMe-ContactItem:last-child{margin-bottom:0}.AboutMe-EducationContainer{margin-left:20px}.AboutMe-Specialty{margin-bottom:4px;font-size:16px;font-weight:500}.AboutMe-University{margin-bottom:4px}.AboutMe-ExpirationDate{font-style:italic;font-size:12px;color:#616161}.AboutMe-SkillsList{margin-left:20px;padding-left:18px;list-style-type:disc}.AboutMe-SkillsItem{margin-bottom:4px}.AboutMe-SkillsItem:last-child{margin-bottom:0}.AboutMe-LanguagesList{margin-left:20px}.AboutMe-LanguagesItem{display:flex;margin-bottom:4px}.AboutMe-LanguagesItemValue{margin-left:0.3em}@media screen and (max-width: 550px){.AboutMe-LanguagesItem{flex-direction:column;margin-bottom:8px;font-weight:500}.AboutMe-LanguagesItemValue{font-weight:normal}}.AboutMe-LanguagesItem:last-child{margin-bottom:0}.AboutMe-AboutText{text-indent:20px}

.Registration{display:flex;align-items:center;flex-direction:column;width:100%;max-width:560px;margin:0 auto;padding:32px;border-radius:10px;background-color:#fff}.Registration-Heading{font-weight:500;font-size:24px;line-height:28px;text-transform:uppercase;margin-bottom:32px}.Registration-Form{display:flex;flex-direction:column;width:100%}.Registration-TextField.MuiFormControl-root{width:100%;margin-bottom:24px}.Registration-Switch.MuiFormControlLabel-root{display:flex;margin:0 0 24px 0}.Registration-Switch.MuiFormControlLabel-root .MuiFormControlLabel-label{font-size:16px;line-height:16px;color:#808080}.Registration-Switch.MuiFormControlLabel-root .MuiSwitch-root{margin-left:-8px}.Registration-Switch.MuiFormControlLabel-root .MuiSwitch-root .MuiButtonBase-root.Mui-checked{color:#9374EB}.Registration-Switch.MuiFormControlLabel-root .MuiSwitch-root .MuiButtonBase-root.Mui-checked+.MuiSwitch-track{background-color:#9374EB}.Registration-Button.MuiButton-root{position:relative;border-radius:5px;padding:12px 0;font-weight:bold;font-size:14px;letter-spacing:1px;line-height:16px;color:#fff;background:linear-gradient(90deg, #9374EB 0%, #E69FD6 100%)}.Registration-Button.MuiButton-root .MuiButton-label{z-index:1}.Registration-Button.MuiButton-root:before{content:'';position:absolute;top:0;left:0;width:100%;height:100%;border-radius:5px;background:linear-gradient(90deg, #E69FD6 0%, #9374EB 100%);opacity:0;transition:0.7s}.Registration-Button.MuiButton-root:hover:before{opacity:1}

